import interfaceurl from '@/utils/interface.js'
export default {
	data() {
		return {
			myReservations: [] //我的预约列表
		}
	},
	onLoad() {

	},
	onShow() {
		this.getMyReservation()
	},
	methods: {
		getMyReservation() {
			let that = this;
			interfaceurl.checkAuth(interfaceurl.myReservation, {}).then((res) => {
				that.myReservations = res.data
			});
		}
	}
}
